@import '../../../styles/mixins';

.undercover-game {
  &__phase-indicator {
    margin-bottom: spacing(8);
    
    @include mobile-only {
      margin-bottom: spacing(6);
    }
  }
  
  &__content {
    min-height: 400px;
  }
}

.phase-steps {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: spacing(4);
  
  @include mobile-only {
    gap: spacing(2);
    overflow-x: auto;
    padding: spacing(2) 0;
  }
}

.phase-step {
  @include flex-center;
  flex-direction: column;
  gap: spacing(2);
  padding: spacing(3);
  min-width: 80px;
  position: relative;
  
  @include mobile-only {
    min-width: 60px;
    padding: spacing(2);
    gap: spacing(1);
  }
  
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    right: -#{spacing(2)};
    width: spacing(4);
    height: 2px;
    background: color(border, medium);
    transform: translateY(-50%);
    
    @include mobile-only {
      right: -#{spacing(1)};
      width: spacing(2);
    }
  }
  
  &:last-child::after {
    display: none;
  }
  
  &--active {
    .phase-step__number {
      background: color(primary, 600);
      color: white;
      transform: scale(1.1);
      box-shadow: 0 0 0 4px rgba(124, 58, 237, 0.2);
    }
    
    .phase-step__label {
      color: color(primary, 700);
      font-weight: map-get(map-get($typography, font-weights), semibold);
    }
    
    &::after {
      background: color(primary, 400);
    }
  }
  
  &__number {
    @include flex-center;
    width: 32px;
    height: 32px;
    background: color(background, tertiary);
    color: color(text, secondary);
    border-radius: map-get($border-radius, full);
    @include text-style(sm, semibold);
    transition: all 300ms ease;
    
    @include mobile-only {
      width: 28px;
      height: 28px;
      @include text-style(xs, semibold);
    }
  }
  
  &__label {
    @include text-style(sm, medium);
    color: color(text, tertiary);
    text-align: center;
    transition: transition(colors);
    white-space: nowrap;
    
    @include mobile-only {
      @include text-style(xs, medium);
    }
  }
}
