@import '../../../../styles/mixins';

.assignment-phase {
  max-width: 600px;
  margin: 0 auto;
  
  &--loading {
    @include flex-center;
    min-height: 400px;
  }
  
  &__header {
    text-align: center;
    margin-bottom: spacing(8);
    
    @include mobile-only {
      margin-bottom: spacing(6);
    }
  }
  
  &__title {
    @include text-style('3xl', bold, tight);
    color: color(text, primary);
    margin-bottom: spacing(4);
    
    @include mobile-only {
      @include text-style('2xl', bold, tight);
    }
  }
  
  &__progress {
    max-width: 400px;
    margin: 0 auto;
  }
  
  &__actions {
    text-align: center;
    margin-top: spacing(6);
  }
}

.progress-text {
  @include text-style('sm', medium);
  color: color(text, secondary);
  display: block;
  margin-bottom: spacing(2);
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: color(background, tertiary);
  border-radius: map-get($border-radius, full);
  overflow: hidden;
  
  &__fill {
    height: 100%;
    background: linear-gradient(90deg, color(primary, 500), color(primary, 600));
    border-radius: map-get($border-radius, full);
    transition: width 500ms ease;
  }
}

.loading-spinner {
  @include flex-center;
  flex-direction: column;
  gap: spacing(4);
  
  .spinner {
    width: 48px;
    height: 48px;
    border: 4px solid color(background, tertiary);
    border-top: 4px solid color(primary, 600);
    border-radius: map-get($border-radius, full);
    animation: spin 1s linear infinite;
  }
  
  p {
    @include text-style('base', medium);
    color: color(text, secondary);
  }
}

.ai-indicator {
  @include flex-center;
  gap: spacing(2);
  margin-top: spacing(2);
  padding: spacing(2) spacing(4);
  background: linear-gradient(135deg, rgba(124, 58, 237, 0.1), rgba(168, 85, 247, 0.1));
  border: 1px solid rgba(124, 58, 237, 0.2);
  border-radius: map-get($border-radius, full);
  
  .ai-icon {
    @include text-style('sm');
    animation: pulse 2s ease-in-out infinite;
  }
  
  .ai-text {
    @include text-style('xs', medium);
    color: color(primary, 700);
  }
}

.error-actions {
  text-align: center;
  margin-top: spacing(4);
  
  .retry-btn {
    @include flex-center;
    gap: spacing(2);
    margin: 0 auto spacing(3);
    padding: spacing(3) spacing(6);
    background: color(primary, 600);
    color: white;
    border: none;
    border-radius: map-get($border-radius, lg);
    @include text-style('base', medium);
    cursor: pointer;
    transition: all 0.2s ease;
    
    &:hover {
      background: color(primary, 700);
      transform: translateY(-1px);
    }
    
    &:active {
      transform: translateY(0);
    }
  }
  
  .error-help {
    @include text-style('sm');
    color: color(text, tertiary);
    max-width: 300px;
    margin: 0 auto;
    line-height: 1.5;
  }
}

.preview-message {
  text-align: center;

  p {
    @include text-style('sm', medium);
    color: color(text, secondary);
    background: color(background, secondary);
    padding: spacing(3) spacing(4);
    border-radius: map-get($border-radius, md);
    margin: 0;
    border: 1px solid color(border, light);

    &:hover {
      background: color(background, primary);
      border-color: color(primary, 200);
    }
  }
}

.player-card {
  @include card(true);
  padding: spacing(8);
  text-align: center;
  
  @include mobile-only {
    padding: spacing(6);
  }
  
  &__header {
    margin-bottom: spacing(6);
  }
  
  &__name {
    @include text-style('2xl', bold, tight);
    color: color(text, primary);
    margin-bottom: spacing(3);
    
    @include mobile-only {
      @include text-style('xl', bold, tight);
    }
  }
}

.role-indicator {
  @include flex-center;
  gap: spacing(2);
  padding: spacing(2) spacing(4);
  border-radius: map-get($border-radius, full);
  @include text-style('sm', semibold);
  display: inline-flex;
  
  &--regular {
    background: rgba(59, 130, 246, 0.1);
    color: #1d4ed8;
    border: 1px solid rgba(59, 130, 246, 0.2);
  }
  
  &--undercover {
    background: rgba(245, 158, 11, 0.1);
    color: #d97706;
    border: 1px solid rgba(245, 158, 11, 0.2);
  }
  
  &--mrwhite {
    background: rgba(107, 114, 128, 0.1);
    color: #374151;
    border: 1px solid rgba(107, 114, 128, 0.2);
  }
}

.word-reveal-section {
  margin: spacing(6) 0;
  min-height: 200px;
  @include flex-center;
}

.mr-white-message {
  @include flex-center;
  flex-direction: column;
  gap: spacing(2);
  padding: spacing(6);
  border-radius: map-get($border-radius, xl);
  
  &__icon {
    @include text-style('4xl');
    
    @include mobile-only {
      @include text-style('3xl');
    }
  }
  
  &__title {
    @include text-style('xl', bold, tight);
    color: color(text, inverse);
    margin: 0;
    
    @include mobile-only {
      @include text-style('lg', bold, tight);
    }
  }
  
  &__description {
    @include text-style('base', normal, relaxed);
    color: color(text, inverse);
    margin: 0;
    text-align: center;
    max-width: 300px;
    
    @include mobile-only {
      @include text-style('sm', normal, relaxed);
    }
  }
}

.word-container {
  perspective: 1000px;
  width: 300px;
  height: 200px;
  
  @include mobile-only {
    width: 280px;
    height: 180px;
  }
}

.word-curtain {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform 800ms ease;
  
  &--revealed {
    transform: rotateY(180deg);
  }
  
  &__front,
  &__back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    border-radius: map-get($border-radius, xl);
    @include flex-center;
    flex-direction: column;
    gap: spacing(3);
  }
  
  &__front {
    background: linear-gradient(135deg, color(primary, 100), color(primary, 200));
    border: 2px solid color(primary, 300);
    
    .curtain-icon {
      @include text-style('3xl');
      
      @include mobile-only {
        @include text-style('2xl');
      }
    }
    
    p {
      @include text-style('base', medium);
      color: color(text, secondary);
      margin: 0;
      text-align: center;
      
      @include mobile-only {
        @include text-style('sm', medium);
      }
    }
  }
  
  &__back {
    background: linear-gradient(135deg, color(primary, 600), color(primary, 700));
    border: 2px solid color(primary, 800);
    transform: rotateY(180deg);
  }
}

.reveal-btn {
  @include button-primary;
  padding: spacing(3) spacing(6);
  @include text-style('sm', semibold);
  
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  @include mobile-only {
    padding: spacing(2) spacing(4);
    @include text-style('xs', semibold);
  }
}

.revealed-word {
  @include flex-center;
  flex-direction: column;
  gap: spacing(2);
  color: white;
  text-align: center;
  
  .word-icon {
    @include text-style('3xl');
    
    @include mobile-only {
      @include text-style('2xl');
    }
  }
  
  .word-text {
    @include text-style('2xl', bold, tight);
    margin: spacing(2) 0;
    
    @include mobile-only {
      @include text-style('xl', bold, tight);
    }
  }
  
  .word-role {
    @include text-style('sm', medium);
    opacity: 0.9;
    
    @include mobile-only {
      @include text-style('xs', medium);
    }
  }
}

.next-btn {
  @include flex-center;
  gap: spacing(2);
  padding: spacing(4) spacing(8);
  background: linear-gradient(135deg, color(primary, 600), color(primary, 700));
  color: white;
  border: none;
  border-radius: map-get($border-radius, xl);
  cursor: pointer;
  @include text-style('lg', semibold);
  transition: all 300ms ease;
  display: inline-flex;
  
  &:hover:not(:disabled) {
    background: linear-gradient(135deg, color(primary, 700), color(primary, 800));
    transform: translateY(-2px);
    box-shadow: shadow(lg);
  }
  
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    
    &:hover {
      transform: none;
      box-shadow: none;
    }
  }
  
  &__icon {
    @include text-style('lg');
  }
  
  @include mobile-only {
    padding: spacing(3) spacing(6);
    @include text-style('base', semibold);
  }
}

// Animations
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes pulse {
  0%, 100% { 
    opacity: 1; 
    transform: scale(1);
  }
  50% { 
    opacity: 0.7; 
    transform: scale(1.1);
  }
}
