@import '../../../../styles/tokens';
@import '../../../../styles/mixins';

.results-phase {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  
  &__header {
    text-align: center;
    margin-bottom: 3rem;
  }
  
  &__content {
    display: flex;
    flex-direction: column;
    gap: 3rem;
  }
  
  &__actions {
    text-align: center;
    margin-top: 3rem;
  }
}

.winner-announcement {
  padding: 2rem;
  border-radius: 16px;
  background: linear-gradient(135deg, var(--color-primary-50) 0%, var(--color-primary-100) 100%);
  border: 2px solid var(--color-primary-200);
  
  &--civilians {
    background: linear-gradient(135deg, var(--color-success-50) 0%, var(--color-success-100) 100%);
    border-color: var(--color-success-200);
  }
  
  &--undercovers {
    background: linear-gradient(135deg, var(--color-warning-50) 0%, var(--color-warning-100) 100%);
    border-color: var(--color-warning-200);
  }
}

.winner-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.winner-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0 0 0.5rem 0;
  color: var(--color-text-primary);
}

.winner-subtitle {
  font-size: 1.2rem;
  color: var(--color-text-secondary);
  margin: 0;
}

.game-words {
  h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 1.5rem;
    color: var(--color-text-primary);
    text-align: center;
  }
}

.words-display {
  display: flex;
  gap: 2rem;
  justify-content: center;
  flex-wrap: wrap;
}

.word-card {
  background: var(--color-surface);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 1.5rem;
  text-align: center;
  min-width: 200px;
  box-shadow: var(--shadow-sm);
  
  &--civilian {
    border-color: var(--color-success-200);
    background: var(--color-success-50);
  }
  
  &--undercover {
    border-color: var(--color-warning-200);
    background: var(--color-warning-50);
  }
}

.word-label {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--color-text-secondary);
  margin-bottom: 0.5rem;
}

.word-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--color-text-primary);
}

.player-roles {
  h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 2rem;
    color: var(--color-text-primary);
    text-align: center;
  }
}

.role-group {
  margin-bottom: 2rem;
  
  &__title {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 1rem;
    color: var(--color-text-primary);
    
    .role-icon {
      font-size: 1.5rem;
    }
  }
  
  &__players {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1rem;
  }
}

.player-card {
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: 8px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  transition: all 0.2s ease;
  
  &:hover {
    box-shadow: var(--shadow-md);
    transform: translateY(-2px);
  }
  
  &--eliminated {
    opacity: 0.7;
    background: var(--color-surface-secondary);
    border-color: var(--color-border-secondary);
  }
}

.player-name {
  font-weight: 600;
  font-size: 1.1rem;
  color: var(--color-text-primary);
}

.player-word {
  font-style: italic;
  color: var(--color-text-secondary);
  font-size: 0.9rem;
}

.elimination-badge {
  background: var(--color-error-100);
  color: var(--color-error-700);
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 500;
  align-self: flex-start;
}

.play-again-btn {
  @include button-primary;
  font-size: 1.2rem;
  padding: 1rem 2rem;
  border-radius: 12px;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  
  &:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-lg);
  }
}

// Responsive design
@media (max-width: 768px) {
  .results-phase {
    padding: 1rem;
    
    &__content {
      gap: 2rem;
    }
  }
  
  .winner-announcement {
    padding: 1.5rem;
  }
  
  .winner-title {
    font-size: 2rem;
  }
  
  .winner-icon {
    font-size: 3rem;
  }
  
  .words-display {
    flex-direction: column;
    align-items: center;
  }
  
  .word-card {
    min-width: auto;
    width: 100%;
    max-width: 300px;
  }
  
  .role-group__players {
    grid-template-columns: 1fr;
  }
}
