@import '../../styles/mixins';

.header {
  position: sticky;
  top: 0;
  z-index: map-get($z-index, sticky);
  background: white;
  backdrop-filter: blur(10px);
  border-bottom: 1px solid color(border, light);
  box-shadow: shadow(md);
  
  &__content {
    @include flex-between;
    padding: spacing(4) 0;
    gap: spacing(4);
  }
  
  &__logo {
    @include flex-center;
    gap: spacing(3);
    
    &-icon {
      @include flex-center;
      width: 40px;
      height: 40px;
      background: color(primary, 600);
      color: white;
      border-radius: map-get($border-radius, lg);
    }
    
    &-text {
      @include text-style(xl, bold);
      color: color(text, primary);
      @include flex-center;
      gap: 0;
    }
  }
  
  &__author {
    @include text-style(sm, normal);
    color: color(text, secondary);
    font-weight: 400;
    margin-left: spacing(2);
    margin-top: 2px;
    
    @include mobile-only {
      display: none;
    }
  }
  
  &__nav {
    @include mobile-only {
      display: none;
    }
    
    &-list {
      @include flex-center;
      gap: spacing(8);
      list-style: none;
      margin: 0;
      padding: 0;
    }
    
    &-link {
      @include text-style(sm, medium);
      color: color(text, secondary);
      padding: spacing(2) spacing(3);
      border-radius: map-get($border-radius, md);
      transition: transition(colors);
      background: none;
      border: none;
      cursor: pointer;
      text-decoration: none;
      
      &:hover {
        color: color(primary, 600);
        background: color(background, secondary);
        text-decoration: none;
      }
      
      &--active {
        color: white;
        background: color(primary, 600);
        font-weight: 600;
        
        &:hover {
          color: white;
          background: color(primary, 700);
          text-decoration: none;
        }
      }
      
      &--subdued {
        opacity: 0.7;
        
        &:hover {
          color: color(primary, 600);
          background: color(background, secondary);
        }
      }
      
      &--play {
        @include flex-center;
        gap: spacing(2);
        background: linear-gradient(135deg, #4c1d95, #7c3aed);
        color: white;
        font-weight: 600;
        position: relative;
        overflow: hidden;
        animation: playButtonGlow 3s ease-in-out infinite;
        
        &:hover {
          background: linear-gradient(135deg, #5b21b6, #8b5cf6);
          transform: translateY(-1px);
          box-shadow: 0 4px 12px rgba(124, 58, 237, 0.4);
        }
        
        .sparkle-animation {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          pointer-events: none;
          
          .sparkle {
            position: absolute;
            @include text-style(xs);
            animation: sparkleFloat 2s ease-in-out infinite;
            
            &:nth-child(1) {
              top: 20%;
              left: 15%;
              animation-delay: 0s;
            }
            
            &:nth-child(2) {
              top: 60%;
              right: 15%;
              animation-delay: 0.7s;
            }
            
            &:nth-child(3) {
              bottom: 20%;
              left: 50%;
              animation-delay: 1.4s;
            }
          }
        }
      }
    }
  }
  
  &__actions {
    @include flex-center;
    gap: spacing(3);
    
    @include mobile-only {
      display: none;
    }
  }
  
  &__theme-toggle {
    @include flex-center;
    width: 40px;
    height: 40px;
    background: none;
    border: 1px solid color(border, light);
    border-radius: map-get($border-radius, md);
    color: color(text, secondary);
    cursor: pointer;
    transition: transition(colors);
    
    &:hover {
      background: color(background, secondary);
      border-color: color(primary, 600);
      color: color(primary, 600);
    }
  }
  
  &__user-menu {
    position: relative;
  }
  
  &__user-button {
    @include flex-center;
    gap: spacing(2);
    background: color(background, secondary);
    border: 1px solid color(border, light);
    border-radius: map-get($border-radius, full);
    padding: spacing(2) spacing(3);
    color: color(text, primary);
    cursor: pointer;
    transition: transition(colors);
    
    &:hover {
      background: color(primary, 50);
      border-color: color(primary, 600);
    }
  }
  
  &__user-avatar {
    @include flex-center;
    width: 28px;
    height: 28px;
    background: color(primary, 100);
    color: color(primary, 600);
    border-radius: map-get($border-radius, full);
    
    &--large {
      width: 40px;
      height: 40px;
    }
  }
  
  &__user-name {
    @include text-style(sm, medium);
    color: color(text, primary);
  }
  
  &__user-dropdown {
    position: absolute;
    top: calc(100% + spacing(2));
    right: 0;
    width: 280px;
    background: white;
    border: 1px solid color(border, light);
    border-radius: map-get($border-radius, xl);
    box-shadow: shadow(xl);
    z-index: map-get($z-index, dropdown);
    overflow: hidden;
    @include scale-in;
  }
  
  &__user-info {
    @include flex-center;
    gap: spacing(3);
    padding: spacing(4);
    border-bottom: 1px solid color(border, light);
  }
  
  &__user-name-large {
    @include text-style(base, semibold);
    color: color(text, primary);
  }
  
  &__user-email {
    @include text-style(sm, normal);
    color: color(text, secondary);
  }
  
  &__user-actions {
    padding: spacing(2);
  }
  
  &__user-action {
    @include flex-center;
    gap: spacing(3);
    width: 100%;
    padding: spacing(3);
    background: none;
    border: none;
    border-radius: map-get($border-radius, md);
    @include text-style(sm, medium);
    color: color(text, secondary);
    cursor: pointer;
    transition: transition(colors);
    text-align: left;
    
    &:hover {
      background: color(background, secondary);
      color: color(text, primary);
    }
    
    &:last-child {
      color: color(error);
      
      &:hover {
        background: rgba(239, 68, 68, 0.1);
        color: color(error);
      }
    }
  }
  
  &__mobile-toggle {
    @include flex-center;
    width: 40px;
    height: 40px;
    background: none;
    border: none;
    color: color(text, secondary);
    cursor: pointer;
    
    @include tablet-up {
      display: none;
    }
    
    &:hover {
      color: color(primary, 600);
    }
  }
  
  &__mobile-menu {
    @include tablet-up {
      display: none;
    }
    
    background: white;
    border-top: 1px solid color(border, light);
    padding: spacing(4) 0;
    @include slide-in-up(200ms);
  }
  
  &__mobile-nav {
    &-list {
      list-style: none;
      margin: 0;
      padding: 0;
      margin-bottom: spacing(6);
    }
    
    &-link {
      display: block;
      @include text-style(base, medium);
      color: color(text, secondary);
      padding: spacing(3) 0;
      border-bottom: 1px solid color(border, light);
      transition: transition(colors);
      background: none;
      border: none;
      cursor: pointer;
      text-decoration: none;
      text-align: left;
      width: 100%;
      
      &:hover {
        color: color(primary, 600);
        text-decoration: none;
      }
      
      &--active {
        color: color(primary, 600);
        font-weight: 600;
        background: color(primary, 50);
        padding: spacing(3) spacing(4);
        margin: 0 spacing(-4);
        border-radius: map-get($border-radius, md);
        
        &:hover {
          color: color(primary, 700);
          background: color(primary, 100);
          text-decoration: none;
        }
      }
      
      &--subdued {
        opacity: 0.7;
        
        &:hover {
          color: color(primary, 600);
          background: color(background, secondary);
        }
      }
      
      &--play {
        @include flex-center;
        justify-content: flex-start;
        gap: spacing(2);
        background: linear-gradient(135deg, #4c1d95, #7c3aed);
        color: white;
        font-weight: 600;
        padding: spacing(3) spacing(4);
        margin: 0 spacing(-4);
        border-radius: map-get($border-radius, md);
        border-bottom: none;
        
        &:hover {
          background: linear-gradient(135deg, #5b21b6, #8b5cf6);
        }
      }
      
      &:last-child {
        border-bottom: none;
      }
    }
  }
  
  &__mobile-actions {
    display: none; // Hide the Get Started button in mobile view
    padding-top: spacing(4);
    border-top: 1px solid color(border, light);
  }
}

// Animations for Let's Play button
@keyframes playButtonGlow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(124, 58, 237, 0.3);
  }
  50% {
    box-shadow: 0 0 20px rgba(124, 58, 237, 0.6), 0 0 30px rgba(124, 58, 237, 0.4);
  }
}

@keyframes sparkleFloat {
  0%, 100% {
    opacity: 0;
    transform: translateY(0px) scale(0.5);
  }
  50% {
    opacity: 1;
    transform: translateY(-3px) scale(1);
  }
}
