@import '../../../../styles/mixins';

.setup-phase {
  max-width: 800px;
  margin: 0 auto;
  
  &__header {
    text-align: center;
    margin-bottom: spacing(8);
    
    @include mobile-only {
      margin-bottom: spacing(6);
    }
  }
  
  &__title {
    @include text-style('3xl', bold, tight);
    color: color(text, primary);
    margin-bottom: spacing(3);
    
    @include mobile-only {
      @include text-style('2xl', bold, tight);
    }
  }
  
  &__description {
    @include text-style('lg', normal, relaxed);
    color: color(text, secondary);
    max-width: 500px;
    margin: 0 auto;
    
    @include mobile-only {
      @include text-style('base', normal, relaxed);
    }
  }
}

.setup-form {
  display: flex;
  flex-direction: column;
  gap: spacing(8);
  
  @include mobile-only {
    gap: spacing(6);
  }
}

.setup-section {
  background: white;
  border: 1px solid color(border, light);
  border-radius: map-get($border-radius, xl);
  padding: spacing(6);
  box-shadow: shadow(sm);
  transition: all 300ms ease;
  
  &:hover {
    box-shadow: shadow(md);
    transform: translateY(-2px);
  }
  
  @include mobile-only {
    padding: spacing(4);
  }
  
  &__header {
    @include flex-between;
    align-items: center;
    margin-bottom: spacing(4);
    
    @include mobile-only {
      flex-direction: column;
      align-items: flex-start;
      gap: spacing(3);
    }
  }
  
  &__title {
    @include flex-center;
    gap: spacing(3);
    @include text-style('xl', semibold, tight);
    color: color(text, primary);
    margin-bottom: spacing(4);
    
    @include mobile-only {
      @include text-style('lg', semibold, tight);
      gap: spacing(2);
    }
  }
  
  &__icon {
    @include text-style('2xl');
    
    @include mobile-only {
      @include text-style('xl');
    }
  }
  
  &__note {
    @include text-style('sm', normal);
    color: color(text, tertiary);
    text-align: center;
    margin-top: spacing(3);
    margin-bottom: spacing(4);
  }
}

// Category Toggles
.category-toggles {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: spacing(3);
  
  @include mobile-only {
    grid-template-columns: 1fr;
    gap: spacing(2);
  }
}

.category-toggle {
  @include flex-center;
  gap: spacing(2);
  padding: spacing(3) spacing(4);
  background: white;
  border: 2px solid color(border, light);
  border-radius: map-get($border-radius, lg);
  cursor: pointer;
  transition: all 300ms ease;
  position: relative;
  
  &:hover:not(:disabled) {
    border-color: color(primary, 300);
    transform: translateY(-2px);
    box-shadow: shadow(md);
  }
  
  &--active {
    border-color: color(primary, 500);
    background: color(primary, 50);
    
    .category-toggle__name {
      color: color(primary, 700);
      font-weight: map-get(map-get($typography, font-weights), semibold);
    }
  }
  
  &--locked {
    cursor: not-allowed;
    opacity: 0.8;
    
    &:hover {
      transform: none;
      box-shadow: none;
    }
  }
  
  &__icon {
    @include text-style('xl');
    
    @include mobile-only {
      @include text-style('lg');
    }
  }
  
  &__name {
    @include text-style('sm', medium);
    color: color(text, secondary);
    transition: transition(colors);
    text-align: center;
    
    @include mobile-only {
      @include text-style('xs', medium);
    }
  }
  
  &__lock {
    position: absolute;
    top: spacing(1);
    right: spacing(1);
    @include text-style('xs');
    opacity: 0.7;
  }
}

// Player Count Selector
.player-count-selector {
  @include flex-center;
  gap: spacing(4);
  margin: spacing(4) 0;
  
  @include mobile-only {
    gap: spacing(3);
  }
}

.count-btn {
  @include flex-center;
  width: 48px;
  height: 48px;
  background: color(primary, 600);
  color: white;
  border: none;
  border-radius: map-get($border-radius, full);
  cursor: pointer;
  transition: all 200ms ease;
  @include text-style('xl', bold);
  
  &:hover:not(:disabled) {
    background: color(primary, 700);
    transform: scale(1.1);
  }
  
  &:active:not(:disabled) {
    transform: scale(0.95);
  }
  
  &:disabled {
    background: color(background, tertiary);
    color: color(text, tertiary);
    cursor: not-allowed;
  }
  
  @include mobile-only {
    width: 40px;
    height: 40px;
    @include text-style('lg', bold);
  }
}

.count-display {
  @include flex-center;
  flex-direction: column;
  gap: spacing(1);
  min-width: 100px;
  
  .count-number {
    @include text-style('4xl', bold, tight);
    color: color(primary, 700);
    
    @include mobile-only {
      @include text-style('3xl', bold, tight);
    }
  }
  
  .count-label {
    @include text-style('sm', medium);
    color: color(text, secondary);
    text-transform: uppercase;
    letter-spacing: map-get(map-get($typography, letter-spacings), wide);
  }
}

// Custom Toggle
.custom-toggle {
  @include flex-center;
  gap: spacing(2);
  cursor: pointer;
  
  input {
    display: none;
  }
  
  &__slider {
    position: relative;
    width: 44px;
    height: 24px;
    background: color(background, tertiary);
    border-radius: map-get($border-radius, full);
    transition: all 200ms ease;
    
    &::after {
      content: '';
      position: absolute;
      top: 2px;
      left: 2px;
      width: 20px;
      height: 20px;
      background: white;
      border-radius: map-get($border-radius, full);
      transition: all 200ms ease;
      box-shadow: shadow(sm);
    }
  }
  
  &__label {
    @include text-style('sm', medium);
    color: color(text, secondary);
  }
  
  input:checked + &__slider {
    background: color(primary, 600);
    
    &::after {
      transform: translateX(20px);
    }
  }
  
  input:checked ~ &__label {
    color: color(primary, 700);
  }
}

// Role Cards
.role-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: spacing(4);
  
  @include mobile-only {
    grid-template-columns: 1fr;
    gap: spacing(3);
  }
}

.role-card {
  @include card;
  padding: spacing(4);
  text-align: center;
  transition: all 300ms ease;
  
  &:hover {
    transform: translateY(-4px);
    box-shadow: shadow(lg);
  }
  
  &--regular {
    border-left: 4px solid color(accent, blue);
  }
  
  &--undercover {
    border-left: 4px solid color(warning);
  }
  
  &--mrwhite {
    border-left: 4px solid color(text, tertiary);
  }
  
  &__icon {
    @include text-style('3xl');
    margin-bottom: spacing(2);
    
    @include mobile-only {
      @include text-style('2xl');
    }
  }
  
  &__title {
    @include text-style('base', semibold, tight);
    color: color(text, primary);
    margin-bottom: spacing(2);
    
    @include mobile-only {
      @include text-style('sm', semibold, tight);
    }
  }
  
  &__count {
    @include text-style('2xl', bold, tight);
    color: color(primary, 600);
    margin-bottom: spacing(2);
    
    @include mobile-only {
      @include text-style('xl', bold, tight);
    }
  }
  
  &__count-control {
    margin-bottom: spacing(2);
  }
  
  &__description {
    @include text-style('xs', normal);
    color: color(text, tertiary);
    margin: 0;
  }
}

.mini-counter {
  @include flex-center;
  gap: spacing(2);
  
  button {
    @include flex-center;
    width: 28px;
    height: 28px;
    background: color(primary, 100);
    color: color(primary, 700);
    border: none;
    border-radius: map-get($border-radius, sm);
    cursor: pointer;
    @include text-style('sm', bold);
    
    &:hover:not(:disabled) {
      background: color(primary, 200);
    }
    
    &:disabled {
      background: color(background, tertiary);
      color: color(text, tertiary);
      cursor: not-allowed;
    }
  }
  
  span {
    @include text-style('xl', bold);
    color: color(primary, 600);
    min-width: 24px;
    text-align: center;
  }
}

// Player Names
.player-names-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: spacing(4);
  
  @include mobile-only {
    grid-template-columns: 1fr;
    gap: spacing(3);
  }
}

.player-input {
  &__label {
    display: block;
    @include text-style('sm', medium);
    color: color(text, secondary);
    margin-bottom: spacing(2);
  }
  
  &__field {
    @include input-base;
    width: 100%;
  }
}

// Validation Feedback
.validation-feedback {
  background: rgba(239, 68, 68, 0.05);
  border: 1px solid rgba(239, 68, 68, 0.2);
  border-radius: map-get($border-radius, lg);
  padding: spacing(4);
  margin-bottom: spacing(4);
  
  &__title {
    @include text-style('base', semibold, tight);
    color: color(error);
    margin-bottom: spacing(3);
    @include flex-center;
    justify-content: flex-start;
    gap: spacing(2);
  }
  
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    
    li {
      @include text-style('sm', normal);
      color: color(error);
      padding: spacing(1) 0;
      position: relative;
      padding-left: spacing(4);
      
      &::before {
        content: '';
        position: absolute;
        left: 0;
        color: color(error);
        font-weight: bold;
      }
    }
  }
}

// Start Game Button
.setup-actions {
  text-align: center;
  margin-top: spacing(4);
}

.start-game-btn {
  @include flex-center;
  gap: spacing(3);
  padding: spacing(4) spacing(8);
  background: linear-gradient(135deg, color(primary, 600), color(primary, 700));
  color: white;
  border: none;
  border-radius: map-get($border-radius, xl);
  cursor: pointer;
  @include text-style('lg', semibold);
  transition: all 300ms ease;
  position: relative;
  overflow: hidden;
  min-width: 200px;
  display: inline-flex;

  &:hover:not(:disabled) {
    background: linear-gradient(135deg, color(primary, 700), color(primary, 800));
    transform: translateY(-2px);
    box-shadow: shadow(lg);
  }
  
  &:active:not(:disabled) {
    transform: translateY(0);
  }
  
  &:disabled {
    background: color(background, tertiary);
    color: color(text, tertiary);
    cursor: not-allowed;
  }
  
  &--ready {
    animation: pulse 2s ease-in-out infinite;
    
    .start-game-btn__sparkles {
      opacity: 1;
    }
  }
  
  &__icon {
    @include text-style('xl');
  }
  
  &__sparkles {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    opacity: 0;
    transition: opacity 300ms ease;
    
    .sparkle {
      position: absolute;
      @include text-style('sm');
      animation: sparkle 1.5s ease-in-out infinite;
      
      &:nth-child(1) {
        top: 20%;
        left: 20%;
        animation-delay: 0s;
      }
      
      &:nth-child(2) {
        top: 60%;
        right: 20%;
        animation-delay: 0.5s;
      }
      
      &:nth-child(3) {
        bottom: 20%;
        left: 50%;
        animation-delay: 1s;
      }
    }
  }
  
  @include mobile-only {
    padding: spacing(3) spacing(6);
    @include text-style('base', semibold);
    min-width: 160px;
  }
}

// Animations
@keyframes pulse {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(124, 58, 237, 0.4);
  }
  50% {
    box-shadow: 0 0 0 10px rgba(124, 58, 237, 0);
  }
}

@keyframes sparkle {
  0%, 100% {
    opacity: 0;
    transform: scale(0.5) rotate(0deg);
  }
  50% {
    opacity: 1;
    transform: scale(1) rotate(180deg);
  }
}
