@import '../../../../styles/mixins';

.gameplay-phase {
  max-width: 800px;
  margin: 0 auto;
  padding: spacing(4);

  h2 {
    @include text-style('2xl', bold, tight);
    color: color(text, primary);
    margin-bottom: spacing(6);
    text-align: center;
  }

  h3 {
    @include text-style('xl', semibold);
    color: color(text, primary);
    margin-bottom: spacing(4);
  }

  p {
    @include text-style(base, normal, relaxed);
    color: color(text, secondary);
    margin-bottom: spacing(4);
    text-align: center;
  }

  // Game Status
  .game-status {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: spacing(6);
    margin-bottom: spacing(6);

    @include tablet-up {
      grid-template-columns: 1fr;
      gap: spacing(4);
    }
  }

  .alive-players {
    background: color(background, secondary);
    border-radius: map-get($border-radius, lg);
    padding: spacing(4);
    border: 1px solid color(border, light);
  }

  .players-list {
    display: flex;
    flex-direction: column;
    gap: spacing(2);
  }

  .player-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: spacing(2) spacing(3);
    background: white;
    border-radius: map-get($border-radius, md);
    border: 1px solid color(border, light);

    .player-name {
      @include text-style(base, medium);
      color: color(text, primary);
    }

    .player-status {
      @include text-style(sm, normal);
      color: color(success, 600);
      background: color(success, 50);
      padding: spacing(1) spacing(2);
      border-radius: map-get($border-radius, full);
    }
  }

  .game-info {
    display: flex;
    flex-direction: column;
    gap: spacing(3);
    align-items: center;
    justify-content: center;
  }

  // Voting Phase
  .voting-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: spacing(4);
    margin: spacing(6) 0;
  }

  .player-vote-card {
    background: color(background, secondary);
    border-radius: map-get($border-radius, lg);
    padding: spacing(4);
    text-align: center;
    border: 1px solid color(border, light);
    transition: transform 0.2s ease;

    &:hover {
      transform: translateY(-2px);
      box-shadow: shadow(md);
    }

    h3 {
      @include text-style(lg, semibold);
      color: color(text, primary);
      margin-bottom: spacing(3);
    }
  }

  .vote-btn {
    @include flex-center;
    gap: spacing(2);
    padding: spacing(3) spacing(4);
    background: color(warning, 600);
    color: white;
    border: none;
    border-radius: map-get($border-radius, md);
    @include text-style(base, medium);
    cursor: pointer;
    transition: all 0.2s ease;
    width: 100%;

    &:hover:not(:disabled) {
      background: color(warning, 700);
      transform: translateY(-1px);
    }

    &:disabled {
      background: color(text, tertiary);
      cursor: not-allowed;
      transform: none;
    }
  }

  // Vote Results
  .elimination-result {
    text-align: center;
    margin: spacing(6) 0;
  }

  .player-role-reveal {
    display: inline-flex;
    align-items: center;
    gap: spacing(3);
    background: color(background, secondary);
    padding: spacing(4) spacing(6);
    border-radius: map-get($border-radius, lg);
    border: 2px solid color(primary, 500);
    margin: spacing(4) 0;

    .role-icon {
      font-size: 2rem;
    }

    .role-name {
      @include text-style('xl', bold);
      color: color(text, primary);
    }
  }

  // Mr. White Guess
  .mr-white-guess {
    margin-top: spacing(6);
    padding: spacing(4);
    background: color(background, secondary);
    border-radius: map-get($border-radius, lg);
    border: 1px solid color(border, light);
  }

  .guess-input {
    display: flex;
    flex-direction: column;
    gap: spacing(3);
    align-items: center;

    p {
      @include text-style(lg, medium);
      color: color(primary, 600);
      margin: 0;
    }

    .guess-input-field {
      padding: spacing(3) spacing(4);
      border: 2px solid color(primary, 300);
      border-radius: map-get($border-radius, md);
      @include text-style(lg);
      text-align: center;
      width: 100%;
      max-width: 300px;

      &:focus {
        outline: none;
        border-color: color(primary, 500);
        box-shadow: 0 0 0 3px rgba(124, 58, 237, 0.1);
      }
    }
  }

  .guess-result {
    text-align: center;

    p {
      @include text-style(lg);
      margin: spacing(2) 0;
    }

    .guess-correct {
      color: color(success, 600);
      font-weight: 600;
    }

    .guess-incorrect {
      color: color(error, 600);
      font-weight: 600;
    }
  }

  .mr-white-survives {
    margin-top: spacing(4);
    padding: spacing(3);
    background: color(success, 50);
    border: 1px solid color(success, 200);
    border-radius: map-get($border-radius, md);

    p {
      @include text-style(base, medium);
      color: color(success, 700);
      margin: 0;
    }
  }

  // Game Over
  &--game-over {
    .game-results {
      text-align: center;
      margin-top: spacing(6);
    }

    .final-roles {
      margin: spacing(6) 0;
      text-align: left;
      max-width: 500px;
      margin-left: auto;
      margin-right: auto;
    }

    .player-role {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: spacing(2) spacing(3);
      margin-bottom: spacing(2);
      background: color(background, secondary);
      border-radius: map-get($border-radius, md);
      border: 1px solid color(border, light);

      .player-name {
        @include text-style(base, medium);
        color: color(text, primary);
      }

      .player-role-badge {
        @include text-style(sm, medium);
        padding: spacing(1) spacing(2);
        border-radius: map-get($border-radius, full);
        text-transform: capitalize;

        &[class*="regular"] {
          background: color(success, 100);
          color: color(success, 700);
        }

        &[class*="undercover"] {
          background: color(warning, 100);
          color: color(warning, 700);
        }

        &[class*="mrwhite"] {
          background: color(secondary, 100);
          color: color(secondary, 700);
        }
      }

      .player-word {
        @include text-style(sm);
        color: color(text, tertiary);
        font-style: italic;
      }
    }
  }

  // Buttons
  .start-voting-btn,
  .submit-votes-btn,
  .continue-btn,
  .play-again-btn,
  .guess-submit-btn {
    @include flex-center;
    gap: spacing(2);
    padding: spacing(3) spacing(6);
    background: color(primary, 600);
    color: white;
    border: none;
    border-radius: map-get($border-radius, lg);
    @include text-style(base, medium);
    cursor: pointer;
    transition: all 0.2s ease;
    margin: spacing(4) auto 0;
    display: block;

    &:hover:not(:disabled) {
      background: color(primary, 700);
      transform: translateY(-1px);
    }

    &:disabled {
      background: color(text, tertiary);
      cursor: not-allowed;
      transform: none;
    }
  }

  .guess-submit-btn {
    background: color(primary, 600);
    margin-top: spacing(2);
    padding: spacing(2) spacing(4);

    &:disabled {
      background: color(text, tertiary);
    }
  }

  .play-again-btn {
    background: color(success, 600);

    &:hover:not(:disabled) {
      background: color(success, 700);
    }
  }
}
