@import '../../styles/mixins';

.game-section {
  min-height: calc(100vh - 80px);
  
  &__hero {
    background: linear-gradient(135deg, 
      color(primary, 900) 0%, 
      color(primary, 700) 50%,
      color(primary, 800) 100%
    );
    padding: spacing(16) 0 spacing(12);
    text-align: center;
    position: relative;
    overflow: hidden;
    
    // Animated background pattern
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-image: 
        radial-gradient(circle at 20% 30%, rgba(255, 255, 255, 0.1) 0%, transparent 50%),
        radial-gradient(circle at 80% 70%, rgba(255, 255, 255, 0.08) 0%, transparent 50%);
      animation: float 6s ease-in-out infinite;
      pointer-events: none;
    }
    
    @include mobile-only {
      padding: spacing(12) 0 spacing(8);
    }
  }
  
  &__title {
    @include text-style('5xl', bold, tight);
    color: white;
    margin-bottom: spacing(4);
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
    position: relative;
    z-index: 2;
    
    @include mobile-only {
      @include text-style('4xl', bold, tight);
    }
  }
  
  &__description {
    @include text-style('xl', normal, relaxed);
    color: rgba(255, 255, 255, 0.9);
    max-width: 600px;
    margin: 0 auto;
    position: relative;
    z-index: 2;
    
    @include mobile-only {
      @include text-style('lg', normal, relaxed);
    }
  }
  
  &__content {
    padding: spacing(12) 0;
    
    @include mobile-only {
      padding: spacing(8) 0;
    }
  }
}

.game-tabs {
  &__nav {
    display: flex;
    justify-content: center;
    margin-bottom: spacing(8);
    border-bottom: 1px solid color(border, light);
    
    @include mobile-only {
      margin-bottom: spacing(6);
    }
  }
  
  &__tab {
    @include flex-center;
    gap: spacing(3);
    padding: spacing(4) spacing(6);
    background: none;
    border: none;
    border-bottom: 3px solid transparent;
    cursor: pointer;
    transition: all 300ms ease;
    position: relative;
    
    @include mobile-only {
      padding: spacing(3) spacing(4);
      gap: spacing(2);
    }
    
    &:hover {
      background: color(primary, 50);
      transform: translateY(-2px);
    }
    
    &--active {
      border-bottom-color: color(primary, 600);
      background: color(primary, 50);
      
      .game-tabs__tab-icon {
        animation: bounce 1s ease-in-out infinite;
      }
      
      .game-tabs__tab-text {
        color: color(primary, 700);
        font-weight: map-get(map-get($typography, font-weights), semibold);
      }
    }
  }
  
  &__tab-icon {
    @include text-style('2xl');
    display: block;
    
    @include mobile-only {
      @include text-style('xl');
    }
  }
  
  &__tab-text {
    @include text-style('lg', medium);
    color: color(text, secondary);
    transition: transition(colors);
    
    @include mobile-only {
      @include text-style('base', medium);
    }
  }
  
  &__content {
    min-height: 500px;
  }
}

// Animations
@keyframes float {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-10px) rotate(1deg);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-5px);
  }
  60% {
    transform: translateY(-3px);
  }
}
